export default class AppMainController {
  constructor() {
    this.pageControll = 0;
    this.pageNumber = 1;
  }

  resetSliderData() {
    const main = document.querySelector('.main');
    const currentItem = document.querySelector('.current-item');
    const toolTip = document.querySelector('.tool-tip');
    const firstItem = document.querySelector('.first-item');
    const prevItem = document.querySelector('.prev-item');

    firstItem.classList.toggle('slider-item-hidden', true);
    prevItem.classList.toggle('slider-item-hidden', true);
    main.style.transform = `translateX(${this.pageControll})`;
    currentItem.innerHTML = this.pageNumber;
    toolTip.innerHTML = this.pageNumber;
  }

  addSliderHandler() {
    const firstItem = document.querySelector('.first-item');
    const nextItem = document.querySelector('.next-item');
    const prevItem = document.querySelector('.prev-item');
    const currentItem = document.querySelector('.current-item');
    const toolTip = document.querySelector('.tool-tip');
    let innerWidthValue;

    firstItem.addEventListener('click', () => {
      const main = document.querySelector('.main');
      prevItem.classList.toggle('slider-item-hidden', true);
      firstItem.classList.toggle('slider-item-hidden', true);

      this.pageControll = 0;
      this.pageNumber = 1;
      main.style.transform = `translateX(${-this.pageControll}%)`;
      toolTip.innerHTML = `${this.pageNumber}`;
      currentItem.innerHTML = `${this.pageNumber}`;
    });
    firstItem.addEventListener('mousedown', () => {
      toolTip.style.opacity = 1;
      toolTip.innerHTML = '1';
    });
    firstItem.addEventListener('mouseup', () => {
      toolTip.style.opacity = 0;
    });

    prevItem.addEventListener('click', () => {
      const main = document.querySelector('.main');
      innerWidthValue = window.innerWidth;

      this.pageControll -= 100;
      this.pageNumber -= 1;
      toolTip.innerHTML = `${this.pageNumber}`;
      currentItem.innerHTML = `${this.pageNumber}`;
      main.style.transform = `translateX(${-this.pageControll}%)`;

      if (this.pageNumber === 1) {
        prevItem.classList.toggle('slider-item-hidden', true);
      }
      if (this.pageNumber === 2) {
        firstItem.classList.toggle('slider-item-hidden', true);
      }
    });
    prevItem.addEventListener('mousedown', () => {
      toolTip.style.opacity = 1;
      toolTip.innerHTML = this.pageNumber - 1;
    });
    prevItem.addEventListener('mouseup', () => {
      toolTip.style.opacity = 0;
      toolTip.innerHTML = this.pageNumber - 1;
    });

    nextItem.addEventListener('mousedown', () => {
      toolTip.style.opacity = 1;
      toolTip.innerHTML = this.pageNumber + 1;
    });
    nextItem.addEventListener('mouseup', () => {
      toolTip.style.opacity = 0;
    });
    nextItem.addEventListener('click', () => {
      const main = document.querySelector('.main');
      prevItem.classList.remove('slider-item-hidden');


      innerWidthValue = window.innerWidth;
      main.scrollLeft += innerWidthValue;
      this.pageControll += 100;
      this.pageNumber += 1;
      main.style.transform = `translateX(${-this.pageControll}%)`;

      toolTip.innerHTML = this.pageNumber;
      currentItem.innerHTML = this.pageNumber;

      if (currentItem.innerHTML === '3') {
        firstItem.classList.remove('slider-item-hidden');
      }
    });
  }

  addScrollHandler() {
    const body = document.querySelector('body');
    const main = document.querySelector('.main');
    const toolTip = document.querySelector('.tool-tip');
    const currentItem = document.querySelector('.current-item');
    const prevItem = document.querySelector('.prev-item');
    const firstItem = document.querySelector('.first-item');

    let isDown = false;

    let walkPercent;
    let startX;

    body.addEventListener('mousedown', (e) => {
      toolTip.style.opacity = 1;
      isDown = true;
      main.classList.add('active');
      startX = e.pageX;
      main.style.transform = `translateX(${-this.pageControll}%)`;
    });

    body.addEventListener('mouseleave', () => {
      isDown = false;
      main.classList.remove('active');
    });

    body.addEventListener('mouseup', () => {
      if (-walkPercent > 10) {
        this.pageControll += 100;
        this.pageNumber += 1;
      }
      if (-walkPercent < -10 && this.pageControll !== 0) {
        this.pageControll -= 100;
        this.pageNumber -= 1;
      }
      walkPercent = 0;

      main.style.transform = `translateX(${-this.pageControll}%)`;
      toolTip.innerHTML = this.pageNumber;
      currentItem.innerHTML = this.pageNumber;
      toolTip.style.opacity = 0;
      isDown = false;
      main.classList.remove('active');

      if (this.pageNumber === 2) {
        prevItem.classList.remove('slider-item-hidden');
        firstItem.classList.toggle('slider-item-hidden', true);
      }
      if (this.pageNumber === 1) {
        prevItem.classList.toggle('slider-item-hidden', true);
      }

      if (this.pageNumber === 3) {
        firstItem.classList.remove('slider-item-hidden');
      }
    });

    body.addEventListener('mousemove', (e) => {
      if (!isDown) return;

      e.preventDefault();
      const x = e.pageX;
      const walk = x - startX;
      walkPercent = 100 / window.innerWidth * walk;

      main.style.transform = `translateX(${-(this.pageControll + ((-walk) / 20))}%)`;
      if (-walkPercent > 10) {
        toolTip.innerHTML = this.pageNumber + 1;
      }
      if (-walkPercent < 10 && -walkPercent > -10) {
        toolTip.innerHTML = this.pageNumber;
      }
      if (-walkPercent < -10 && this.pageControll !== 0) {
        toolTip.innerHTML = this.pageNumber - 1;
      }
    });
  }
}
