import App from './App';
import AppMainController from './AppMainController';

export default class AppHeaderController {
  static addInputHandler() {
    const inputField = document.querySelector('.input-field');
    const main = document.querySelector('.main');
    const nextItem = document.querySelector('.next-item');


    inputField.addEventListener('keypress', (e) => {
      if (e.keyCode === 13) {
        const app = new App(inputField.value, 'AIzaSyCRo6yvMOCcpbz2sLZojBXP4nWO2mVgAek');
        const sliderController = new AppMainController();
        sliderController.resetSliderData();

        inputField.value = null;
        document.querySelector('.slider-container').classList.remove('slider-hidden');

        if (main.innerHTML === '') {
          app.start();
        } else {
          main.innerHTML = '';
          app.start();
        }

        sliderController.addSliderHandler();
        sliderController.addScrollHandler();


        main.addEventListener('mouseup', () => {
          const bodySlider = document.querySelector('body');
          const innerWidthValue = window.innerWidth;
          if (bodySlider.scrollWidth <= innerWidthValue * 4) {
            app.start();
          }
        });

        nextItem.addEventListener('click', () => {
          const bodySlider = document.querySelector('body');
          const innerWidthValue = window.innerWidth;
          if (bodySlider.scrollWidth <= innerWidthValue * 4) {
            app.start();
          }
        });
      }
    });
  }
}
