export default class AppFooter {
  static setAttributes(node, attr) {
    const elem = document.createElement(node);

    Object.keys(attr).forEach((key) => {
      elem.setAttribute(`${key}`, attr[key]);
    }, attr);

    return elem;
  }

  static render() {
    const footer = AppFooter.setAttributes('footer', {
      class: 'footer',
    });
    const slider = AppFooter.setAttributes('div', {
      class: 'slider-container slider-hidden',
    });

    const firstItem = AppFooter.setAttributes('div', {
      class: 'slider-item first-item slider-item-hidden',
    });
    const prevItem = AppFooter.setAttributes('div', {
      class: 'slider-item prev-item slider-item-hidden',
    });
    const currentItem = AppFooter.setAttributes('div', {
      class: 'slider-item current-item',
    });
    const nextItem = AppFooter.setAttributes('div', {
      class: 'slider-item next-item',
    });
    const toolTip = AppFooter.setAttributes('div', {
      class: 'tool-tip invisible-tool',
    });

    firstItem.innerHTML = '<<';
    prevItem.innerHTML = '<';
    nextItem.innerHTML = '>';
    toolTip.innerHTML = '1';
    currentItem.innerHTML = '1';
    slider.appendChild(firstItem);
    slider.appendChild(prevItem);
    slider.appendChild(currentItem);
    slider.appendChild(nextItem);

    footer.appendChild(toolTip);
    footer.appendChild(slider);

    document.body.appendChild(footer);
  }
}
